export const timer = (ele, count) => {
  let wait = count || 60
  var dom = ele || document.querySelector('.sendBtn')
  time(dom)
  function time (o) {
    if (wait === 0) {
      o.removeAttribute('disabled')
      o.innerHTML = ''
      wait = 60
      return false
    } else {
      o.setAttribute('disabled', true)
      o.innerHTML = `(${wait})`
      wait--
      setTimeout(() => {
        time(o)
      }, 1000)
    }
  }
}

export function inviteCode () {
  const reg = new RegExp('[^&]?' + encodeURI('invcode') + '=[^&]+')
  let array = location.hash.match(reg)
  if (array != null) {
    return {
      inviteCode: decodeURI(array[0].substring(array[0].search('=') + 1)),
      status: true
    }
  } else {
    return {
      inviteCode: null,
      status: false
    }
  }
}

export function scroll (el, callback1, callback2) {
  var count = 1
  el.addEventListener('scroll', () => {
    var h = el.clientHeight
    var sh = el.scrollHeight
    var st = el.scrollTop
    typeof callback2 === 'function' && callback2(st)
    if (h + st >= sh) {
      count++
      // if (Math.ceil(arry.length / 10) < count) {
      //   count = 1
      // }
      callback1(count)
    }
  })
}

export function parseTime (time, cFormat) {
  if (arguments.length === 0) {
    return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const timeStr = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') return ['', '', '', '', '', '', ''][value - 1]
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  return timeStr
}

export function animate (ele, opt, msec, callback) {
  // 
  let timerLen = 0
  // opt
  for (var attr in opt) {
    // attr
    // ES6letattr
    // 
    createTimer(attr)
    timerLen++
  }

  function createTimer (attr) {
    // (1)
    let timerName = attr + 'timer'
    let target = opt[attr]
    clearInterval(ele[timerName])
    // DOM(2)
    ele[timerName] = setInterval(() => {
      // 
      let current = getComputedStyle(ele)[attr] // String:100px, 50rem, 0.5, 60deg
      // 
      // (current)
      let unit = current.match(/[a-z]+$/)
      if (unit) {
        current = current.substring(0, unit.index) * 1
        unit = unit[0]
      } else {
        unit = ''
        current *= 1
      }
      // 
      let speed = (target - current) / 10
      // speedspeed
      // 0.3=>1, -0.3=>-1
      speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed)
      if (attr === 'opacity') {
        speed = speed > 0 ? 0.05 : -0.05
      }
      // 
      if (current >= target) {
        clearInterval(ele[timerName])
        current = target - speed
        timerLen--
        if (typeof callback === 'function' && timerLen === 0) {
          callback()
        }
      }
      // 
      ele.style[attr] = current + speed + unit
    }, msec || 30)
  }
}

// k()
export function maCount (dayCount, data) {
  var result = []
  for (var i = 0, len = data.length; i < len; i++) {
    if (i < dayCount) {
      continue
    }
    var sum = 0
    for (var j = 0; j < dayCount; j++) {
      sum += data[i - j][4]
    }
    result.push(sum / dayCount)
  }
  return 123
}

/**
 * 1. url  base64
 * canvas.toDataURLAPIbase64
 * @param {String} url
 * @return {Promise}
 * @demo
 * let imgUrL = `http://XXX.jpg`
 * this.getDataUri(imgUrL).then(res => {
 *  // base64
 *  console.log('base64', res)
 * })
 */
export function urlToBase64 (url) {
  return new Promise((resolve, reject) => {
    let image = new Image()
    image.onload = function () {
      let canvas = document.createElement('canvas')
      canvas.width = this.naturalWidth
      canvas.height = this.naturalHeight
      // 
      canvas.getContext('2d').drawImage(image, 0, 0)
      // result
      let result = canvas.toDataURL('image/png')
      resolve(result)
    }
    // CORS https://stackoverflow.com/questions/20424279/canvas-todataurl-securityerror
    image.setAttribute('crossOrigin', 'Anonymous')
    image.src = url
    // 
    image.onerror = () => {
      reject(new Error(''))
    }
  })
}

/**
 * 2. base64  blob
 * URL.createObjectURLblobURL
 * @param {Object}
 * @return {Promise}
 * @demo
 * let base64 = base64.split(',')[1]
 * this.base64ToBlob({b64data: base64, contentType: 'image/png'}).then(res => {
 *   // blob
 *   console.log('blob', res)
 * })
 */
export function base64ToBlob ({ b64data = '', contentType = '', sliceSize = 512 } = {}) {
  return new Promise((resolve, reject) => {
    //  atob() 
    let byteCharacters = atob(b64data)
    let byteArrays = []
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      let slice = byteCharacters.slice(offset, offset + sliceSize)
      let byteNumbers = []
      for (let i = 0; i < slice.length; i++) {
        byteNumbers.push(slice.charCodeAt(i))
      }
      // 8  0
      // 
      byteArrays.push(new Uint8Array(byteNumbers))
    }
    let result = new Blob(byteArrays, {
      type: contentType
    })
    result = Object.assign(result, {
      // jartto:  URL.createObjectURL
      preview: URL.createObjectURL(result),
      name: `.png`
    })
    resolve(result)
  })
}

/**
 * 3. blob  base64
 * fileReaderreadAsDataURLblobbase64
 * @param {blob} blob
 * @return {Promise}
 * @demo
 * this.blobToBase64(blob).then(res => {
 *  // base64
 *  console.log('base64', res)
 * })
 */
export function blobToBase64 (blob) {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader()
    fileReader.onload = (e) => {
      resolve(e.target.result)
    }
    // readAsDataURL
    fileReader.readAsDataURL(blob)
    fileReader.onerror = () => {
      reject(new Error(''))
    }
  })
}
